import { RPTooltip } from "@rp/react/web/common/base-components"
import { SpellcheckOutlined as MarkSentences } from "@rp/react/web/mui/icons"
import { CheckboxStyled } from "./RpJsonToolbar.styles"

type Props = {
  markSentences: boolean
  size: "small" | "medium" | undefined
  onChange: (event: boolean) => void
}

export const MarkSentencesButton = ({ markSentences, size, onChange }: Props) => {
  return (
    <RPTooltip title={markSentences ? "Unmark sentences" : "Mark sentences"}>
      <CheckboxStyled
        size={size}
        checked={markSentences}
        onChange={event => {
          onChange(event.target.checked)
        }}
        checkedIcon={<MarkSentences data-testid="RpJsonToolbarMarkSentences" />}
        icon={<MarkSentences />}
        inputProps={{ "aria-label": "Mark sentences" }}
      />
    </RPTooltip>
  )
}
